.card {
    width: 300px;
    height: auto;
    margin: 30px;
    border-radius: 20px;
    border: 2px solid black;
    padding: 10px;
    overflow: hidden;
}
.card img {
    width: 100%;
    border-radius: 20px;
}
.gifs{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}